<template>
  <div>
    <h4 id="category_name">{{ headers }}</h4>
    <div id="post-list">
      <ul>
        <transition-group name="list">
          <li v-for="item in articleLists" :key="item.id">
            <router-link tag="a" :to="{name: 'Detail', params:{id: item.id}}">
              <img :src="item.cover" class="post-img" :alt="item.title"/>
              <div class="article-desc">
                <div class="article-title">
                  <title class="block">{{ item.title }}</title>
                </div>
                <div class="article-docs">
                  <span>{{ item.desc }}</span>
                </div>
                <div class="article">
                  <span class="iconfont">&#xe64d; {{ item.add_time }}&nbsp;&nbsp;&nbsp;&#xe621;{{ item.category.name }}&nbsp;&nbsp;&nbsp;&#xe639; {{ item.pv }}&nbsp;&nbsp;&nbsp;&#xe627;&nbsp;{{ item.like_count }}</span>
                </div>
              </div>
            </router-link>
          </li>
        </transition-group>
      </ul>
      <div id="page">
        <div id="page-btn">
          <div v-if="previous" @click="pageData(previous)" id="previous">上一页</div>
          <div v-if="page_next" @click="pageData(page_next)" id="next">下一页</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {articleList, page_data} from "../../api/api";

  export default {
    name: "Postlist",
    data() {
      return {
        articleList: [],
        headers: '文章列表',
        page_next: "",
        previous: "",
      }
    },
    computed: {
      articleLists: function () {
        return this.articleList;
      }
    },
    methods: {
      initArticle() {
        if (this.$route.query['category']) {
          articleList(this.$route.query['category']).then(this.articleData);
          this.headers = this.$route.query['name'];
        }
        else if (this.$route.query['q']) {
          searchList(this.$route.query['q']).then(this.articleData);
          this.headers = '搜索结果';
        }
        else {
          articleList().then(this.articleData);
          this.headers = '文章列表';
        }
      },
      articleData(response) {
        let list = response.data;
        this.articleList = list.result.results;
        this.page_next = list.next;
        this.articleList = list.result.results;
      },
      pageData(url) {
        var that = this;
        page_data(url).then((response) => {
          that.previous = response.data.previous;
          that.page_next = response.data.next;
          that.articleList = response.data.results;
        });
      }
    },
    mounted() {
      const that = this;
      that.initArticle();
    },
    watch: {
      "$route": "initArticle"
    },
  }
</script>

<style lang="css" scoped>
  #page {
    padding-bottom: 130px;
  }

  #page-btn {
    position: relative;
  }

  #page-btn div {
    display: inline-block;
    line-height: 45px;
    border: solid 1px #b7b7b7;
    box-shadow: 1px 1px 8px #888888;
    border-radius: 10px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 20px;
  }

  #page-btn div:hover {
    transition: 0.5s;
    transform: scale(1.1, 1.1);
    border: solid 1px black;
  }

  #next {
    position: absolute;
    left: 60%;
  }

  #previous {
    position: absolute;
    left: 34%;
  }

  #category_name {
    margin-top: 50px;
  }

  a {
    text-decoration: none;
  }

  .block {
    display: block;
    text-align: center;
    color: #ffff;
    font-weight: bold;
    margin-bottom: 20px;
  }

  #post-list ul li {
    border: #c2c2c2 solid 2px;
    border-radius: 15px;
    overflow: hidden;
    height: 260px;
    margin: 0px 95px 80px;
    position: relative;
    box-shadow: 1px 1px 13px #888888;
  }

  #post-list ul li:hover {
    border: black solid 1px;
  }

  .article-desc {
    position: relative;
    z-index: 999;
  }

  h4 {
    text-align: center;
    margin: 10px;
  }

  #post-list ul {
    margin-top: 60px;
    align-content: center;
    list-style-type: none;
    overflow: hidden;
    padding-top: 40px;
    padding-left: 150px;
    padding-right: 150px;
  }

  li:hover {
    transition: 0.5s;
    transform: scale(1.1, 1.1);
  }

  .post-img {
    width: 100%;
    position: absolute;
    top: -100px;
    z-index: 998;
  }

  .article-desc {
    margin-top: 4%;
  }

  .article-desc span {
    font-size: 15px;
  }

  .article {
    margin-top: 10px;
    margin-left: 34%;
  }

  .article span {
    margin-right: 20px;
    color: #fff;
  }

  .article-docs {
    text-align: center;
  }

  .article-docs span {
    width: 100px;
    font-size: 30px;
    color: #fff;
    text-align: center;
  }

  title {
    font-size: 30px;
  }

  @media only screen and (min-width: 1800px) and (max-width: 2300px) {
    #category_name {
      margin-top: 80px;
      font-size: 3em;
      margin-bottom: 80px;
    }

    #post-list ul li {
      height: 400px;
    }

    .article {
      margin-top: 10px;
      margin-left: 38%;
    }
  }

  @media only screen and (min-width: 2300px) and (max-width: 3000px) {
    #category_name {
      margin-top: 200px;
      font-size: 3em;
      margin-bottom: 200px;
    }

    #post-list ul li {
      height: 420px;
    }

    .article {
      margin-top: 10px;
      margin-left: 38%;
    }
  }

  @media only screen and (min-width: 3000px) and (max-width: 4000px) {
    #category_name {
      margin-top: 200px;
      font-size: 4em;
      margin-bottom: 200px;
    }

    #post-list ul li {
      height: 650px;
    }

    .article {
      margin-top: 10px;
      margin-left: 38%;
    }
  }

  @media only screen and (min-width: 4000px) and (max-width: 8000px) {
    #category_name {
      margin-top: 200px;
      font-size: 8em;
      margin-bottom: 200px;
    }

    #post-list ul li {
      border-radius: 30px;
      height: 1000px;
      margin: 200px 300px 200px;
      position: relative;
      box-shadow: 1px 1px 13px #888888;
    }

    .article-desc {
      margin-top: 8%;
    }

    .article-docs span {
      font-size: 4em;
    }

    .article {
      margin-top: 10px;
      margin-left: 38%;
    }

    .article span {
      margin-right: 60px;
      color: #fff;
    }

    #page {
      text-align: center;
      line-height: 150px;
      border: solid 1px #b7b7b7;
      box-shadow: 1px 1px 1px #888888;
      border-radius: 20px;
      font-size: 60px;
      width: 400px;
    }

    title {
      font-size: 6em;
    }
  }

  @media only screen and (max-width: 900px) {

  }

  @media only screen and (min-width: 200px) and (max-width: 500px) {
    #post-list ul {
      margin-top: 60px;
      -ms-flex-line-pack: center;
      align-content: center;
      list-style-type: none;
      padding: 0px;
    }

    #post-list ul li {
      margin: 0px 20px 80px;
      height: 130px;

    }

    .post-img {
      width: 100%;
      position: absolute;
      top: -1px;
      z-index: 998;
    }

    title {
      font-size: 18px;
    }

    .article {
      margin-top: 10px;
      margin-left: 34%;
    }

    .article-desc span {
      font-size: 10px;
    }

    .article {
      margin-top: 10px;
      margin-left: 15%;
    }

    .article span {
      margin-right: 7px;
    }

    #page-btn div {
      line-height: 35px;
      font-size: 15px;
    }

    #previous {
      position: absolute;
      left: 18%;
    }
  }

  .list-enter-active, .list-leave-active {
    transition: all 1s;
  }

  .list-enter, .list-leave-to
    /* .list-leave-active for below version 2.1.8 */
  {
    opacity: 0;
    transform: translateY(30px);
  }
</style>



