import axios from 'axios'

let localhost = 'https://www.jummy.top';
// let localhost = 'http://127.0.0.1:8000';
let versioning = "/v1";

export const articleList = function (category) {
  if(category) {
    return axios.get(localhost.toString() + versioning +'/aricle/?' + 'category=' + category)
  }
  else {
    return axios.get(localhost.toString() + versioning + '/aricle/')
  }
};

export const articleDetail = function (id) {
  return axios.get(localhost.toString() + versioning +'/aricle/' + id + '/');
};

export const categoryList = () => {
  return axios.get(localhost+ versioning + '/category/')
};

export const gitAuth = (code) => {
  return axios.get(localhost + '/users/git/oauth/?code=' + code);
};

export const home_img = () => {
  return axios.get(localhost + '/home_img/');
};

export const page_data = (url) => {
  return axios.get(url);
};

export const commentList = (comment) => {
  return axios.get(localhost + versioning + '/comment/' + comment);
};

export const create_comments = (params) => {
  return axios.post(localhost + versioning + '/comment/', params)
};
//获取留言板
export const get_comments = (object_id) => {
  return axios.get(localhost + versioning + '/comment/?object_id='+object_id);
};
//留言板分页
export const page_comments = (url) => {
  return axios.get(url);
};
export const comment_like = (params) => {
  return axios.post(localhost + versioning + '/comment/like/', params);
};

export const comment_like_delete = (object_id) => {
  return axios.delete(localhost + versioning + '/comment/like/?object_id='+object_id);
};

// 点赞
export const like = (params,type) => {
  if (type === "create"){
    console.log(params);
    return axios.post(localhost + versioning + '/like/',params);
  } else {
    console.log(params);
    return axios.delete(localhost + versioning + '/like/?object_id='+params.object_id+'&content_type='+params.content_type);
  }
};


