<template>
  <div id="error">
    <img src="http://wx4.sinaimg.cn/large/9f301c37gy1fy1wzi0c0dj20rs0pumzj.jpg" alt="404">
  </div>
</template>

<script>
  export default {
    name: "Vue404",
    data() {
      return {}
    },
    mounted() {
      setTimeout(this.redict_Home, 1500);
    },
    methods: {
      redict_Home() {
        this.$router.push({name: 'Postlist'})
      }
    }
  }
</script>

<style lang="css" scoped>
  img {
    width: 700px;
    position: absolute;
    top: 5%;
    left: 25%;
  }

  @media only screen and (min-width: 200px) and (max-width: 500px) {
    img {
      width: 100%;
      position: absolute;
      top: 18%;
      left: 0;
    }
  }
</style>



