<template>
  <div id="post">
    <div id="home-img">
    </div>
    <transition name="custom-classes-transition" enter-active-class="animated bounceInRight"
                leave-active-class="animated bounceOutRight">
      <router-view @showDowa="showDowa"/>
    </transition>
  </div>
</template>

<script>
  import Postlist from './Postlist'
  import Detail from './Detail'
  import {home_img} from '../../api/api'

  export default {
    name: "Posts",
    data() {
      return {}
    },
    mounted() {
      home_img().then(this.initImg);
    },
    methods: {
      initImg(response) {
        this.img_url = response.data.url;
        let img_tag = $("#home-img");
        img_tag.css({"background-image": "url(" + this.img_url + ")"});
        img_tag.addClass('imgb');
      },
      showDowa() {
        this.$emit('showDowa');
      }
    },
    components: {
      Postlist,
      Detail,
    }
  }
</script>

<style scoped>
  .imgb {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  #home-img {
    display: block;
    height: 350px;
    overflow: hidden;
    opacity: 1;
    box-shadow: 1px 1px 13px #888888;
  }

  #post {
    animation-name: myfirst;
    animation-duration: 1s;
  }

  @media only screen and (min-width: 2100px) and (max-width: 2850px) {
    #home-img {
      height: 500px;
    }
  }

  @media only screen and (min-width: 2860px) and (max-width: 5000px) {
    #home-img {
      height: 600px;
    }
  }

  @media only screen and (min-width: 5000px) and (max-width: 20000px) {
    #home-img {
      height: 1500px;
    }
  }

  @media only screen and (min-width: 300px) and (max-width: 500px) {
    #home-img {
      height: 200px;
    }
  }

  @keyframes myfirst {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
</style>



