<template>
  <div>
    <router-view v-if="isRouterAlive"/>
  </div>
</template>

<script>
  export default {
    name: 'App',
    provide() {
      return {
        reload: this.reload,
      }
    },
    data() {
      return {
        isRouterAlive: true,
      }
    },
    methods: {
      reload() {
        this.isRouterAlive = false;
        this.$nextTick(function () {
          this.isRouterAlive = true;
        });
      }
    }
  }
</script>

<style lang="css">
  html {
    overflow-x: hidden;
    overflow-y: auto;
  }
  body {
    margin: 0px;
    width: 100vw;
    overflow: hidden;
    background-color: #ededed;
  }
</style>



