<template>
  <div id="comment">
    <h2>留言板({{commentcount}})</h2>
    <span>{{error}}</span>
    <textarea v-model="content" maxlength="50"></textarea>
    <button id="hom_btn"
            @click="createComment({object_id:detail_pk,parent_id:0,root_id:0,reply_to_id:0},-1,1)">
      确定
    </button>

    <ul v-for="(item, index) in comment_list">
      <li :key="item.id" class="border_li">
        <div class="comment_list">
          <div class="userinfo">
            <a :href="item.git_path"><img class="img blockis" :src="item.user_img" alt=""></a>
            <span class="blockis blockis_s">{{item.username}}</span>
          </div>
          <div class="leftd" :id="'comment_1'+item.id">
          <span ng-class="leftd_h">
              <img ng-src="./img/c_pic.pn"/>
          </span>
            <div class="speech left" ng-class="speech left">
              {{item.content}}
            </div>
          </div>
          <div class="icon_comment">
            <span class="iconfont">&#xe64d; {{item.time}}</span>
            <span class="iconfont" @click="CommentLike(item.id,index,-1,1)"
                  v-if="item.is_like">&#xe627; {{item.like_num}}</span>
            <span class="iconfont" @click="CommentLike(item.id,index,-1,2)"
                  v-else>&#xe643; {{item.like_num}}</span>
            <span class="iconfont" @click="showText('text_'+item.id)">&#xe63e; 回复</span>
          </div>
          <div class="comment_text comment_btn" :id="'text_'+item.id">
            <textarea v-model="content_sub" class="text"></textarea>
            <div>
              <button
                @click="createComment({object_id:detail_pk,parent_id:item.id,root_id:item.id,reply_to_id:item.user_id}, index, 2)">
                确定
              </button>
              <button @click="closText">
                取消
              </button>
            </div>
          </div>
          <div class="comment_list margin sub_comment"
               v-for="(sub_item,sub_index) in item.root"
               :key="sub_item.id">
            <div class="userinfo_sub">
              <a :href="sub_item.git_path"><img class="img blockis" :src="sub_item.user_img" alt=""></a>
              <span class="blockis blockis_s">{{sub_item.username}}</span>
            </div>
            <div class="leftd">
            <span ng-class="leftd_h">
                <img ng-src="./img/c_pic.pn"/>
            </span>
              <div class="speech left" ng-class="speech left">
                回复@{{sub_item.reply_to}}：{{sub_item.content}}
              </div>
            </div>
            <div class="icon_comment_sub">
              <span class="iconfont">&#xe64d; {{sub_item.time}}</span>
              <span class="iconfont"
                    @click="CommentLike(sub_item.id,index,sub_index,1)"
                    v-if="sub_item.is_like">&#xe627; {{sub_item.like_num}}</span>
              <span class="iconfont"
                    @click="CommentLike(sub_item.id,index,sub_index,2)"
                    v-else>&#xe643; {{sub_item.like_num}}</span>
              <span class="iconfont"
                    @click="showText('rep_text_'+sub_item.id)">&#xe63e; 回复</span>
            </div>
            <div class="comment_text sub_comment_btn"
                 :id="'rep_text_'+sub_item.id">
              <textarea v-model="content_sub" class="text"></textarea>
              <div>
                <button
                  @click="createComment({object_id:detail_pk,parent_id:sub_item.id,root_id:item.id,reply_to_id:sub_item.user_id}, index, 2)">
                  确定
                </button>
                <button @click="closText">
                  取消
                </button>
              </div>
            </div>
          </div>
        </div>
      </li>
    </ul>

    <span class="iconfont load_none load_hover" @click="loadPageData()"
          v-show="load_flag">点击加载更多~</span>
    <span class="iconfont load_none" v-show="load_none">没有啦~</span>
  </div>
</template>

<script>
  import {
    create_comments,
    page_comments,
    get_comments,
    comment_like,
    comment_like_delete
  } from "../../api/api";
  console.log(create_comments);
  export default {
    name: "Comment",
    props: {
      detail_id: ""
    },
    data() {
      return {
        content: "",
        content_sub: "",
        error: "",
        data_index: -1,
        load_flag: true,  // 有或者没有
        load_none: false,
        detail_pk: this.detail_id,
        commentInit: [],
        commentcount: 0,
        commentnext: "",
        commentprevious: "",
      }
    },
    mounted() {
      // 初始化留言数据
      get_comments(this.detail_pk).then(this.comment_Data);
    },
    computed: {
      comment_list: function () {
        return this.commentInit;
      }
    },
    methods: {
      // 文本框关闭
      showText(tag) {
        $(".comment_text").css("display", "none");
        $("#" + tag).css("display", "block");
      },
      closText() {
        $(".comment_text").css("display", "none");
      },
      createComment(parmes, index, type) {
        //创建留言或者子留言 根据index区分 利用vue计算属性监听动态添加数组元素
        if (this.$store.state.userInfo.token === null) {
          
          this.$emit('showDowaHome');
          return
        }
        this.data_index = -1;
        let func = null;
        if (index > -1) {
          this.data_index = Number(index);
          func = this.commentSubData;
        }
        else {
          func = this.commentData;
        }
        if (this.content.length === 0 && this.content_sub.length === 0) {
          this.error = "不能为空";
          return
        }
        if (type === 1) {
          parmes.content = this.content;
        }
        else {
          parmes.content = this.content_sub;
        }
        create_comments(parmes).then(func);
        $(".comment_text").css("display", "none");
      },
      commentData(response) {
        this.commentInit.unshift(response.data);
        this.data_index = -1;
        this.load_none = false;
        this.load_flag = true;
        this.content = "";
        this.commentcount += 1;
      },
      commentSubData(response) {
        this.commentInit[this.data_index].root.push(response.data);
        this.data_index = -1;
        this.content_sub = "";
      },
      pageData(response) {
        for (var i = 0; i < response.data.results.length; i++) {
          this.commentInit.push(response.data.results[i]);
        }
        this.commentnext = response.data.next;
        if (this.commentnext === null) {
          this.load_none = true;
          this.load_flag = false;
        }
      },
      // 分页加载留言
      loadPageData() {
        if (this.commentnext != "" && this.commentnext != null) {
          page_comments(this.commentnext).then(this.pageData);
          this.$emit('comment_Next');
        }
        else {
          this.load_none = true;
          this.load_flag = false;
        }
      },
      // 留言点赞，和加载留言思路类似  id 留言id  c顶级留言的数组下标  sub_c子留言下标 type删除 创建
      CommentLike(id, c, sub_c, type) {
        if (this.$store.state.userInfo.token === null) {
          this.$emit('showDowaHome');
          return
        }
        var that = this;
        if (type === 2) {
            if (sub_c == -1) {
              that.commentInit[c].is_like = true;
            }
            else {
              that.commentInit[c].root[sub_c].is_like = true;
            }
          comment_like({object_id: id}).then(function (response) {
            if (sub_c == -1) {
              that.commentInit[c].like_num = response.data.like_num;
            }
            else {
              that.commentInit[c].root[sub_c].like_num = response.data.like_num;
            }
          });
        }
        else {
          comment_like_delete(id).then(function (response) {
            if (sub_c == -1) {
                that.commentInit[c].is_like = false;
              }
              else {
                that.commentInit[c].root[sub_c].is_like = false;
              }
            if (response.data.status === 200) {
              if (sub_c == -1) {
                if (that.commentInit[c].like_num <= 0) {
                  that.commentInit[c].like_num = 0
                } else {
                  that.commentInit[c].like_num -= 1;
                }
                that.commentInit[c].is_like = false;
              }
              else {
                if (that.commentInit[c].root[sub_c].like_num <= 0) {
                  that.commentInit[c].root[sub_c].like_num = 0
                }
                else {
                  that.commentInit[c].root[sub_c].like_num -= 1;
                }
                that.commentInit[c].root[sub_c].is_like = false;
              }
            }
          });
        }

      },
      comment_Data(response) {
        this.commentInit = response.data.results;
        this.commentcount = response.data.count;
        this.commentnext = response.data.next;
        this.commentprevious = response.data.previous;
      }
    }
  }
</script>

