<template>
  <div class="box">
    <span>登陆中.....</span>
    <div class="loader-15"></div>
  </div>
</template>

<script>
  import {gitAuth} from "../../api/api";
  import Cookie from '../../static/js/cookie'

  export default {
    name: "AuthGit",
    data() {
      return {
        auth_data: {},
        code: ''
      }
    },
    methods: {
      userInfo(response) {
        this.auth_data = response.data;
        Cookie.setCookie('id', this.auth_data.id, 7);
        Cookie.setCookie('name', this.auth_data.name, 7);
        Cookie.setCookie('token', this.auth_data.token, 7);
        this.$store.dispatch('setInfo');
        this.$router.push({name: 'Postlist'})
      }
    },
    created() {
      if (this.$route.query['code']) {
        this.code = this.$route.query['code'];
        gitAuth(this.code).then(this.userInfo);
      } else {

      }
    }
  }
</script>

<style lang="css" scoped>
  .box span {
    display: block;
    margin-top: 15%;
    color: #646262;
  }

  .box {
    border-radius: 3px;
    font-size: 40px;
    position: fixed;
    text-align: center;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    vertical-align: top;
    -webkit-transition: .3s color, .3s border, .3s transform, .3s opacity;
    transition: .3s color, .3s border, .3s transform, .3s opacity;
  }

  .loader-15 {
    background: currentcolor;
    position: relative;
    -webkit-animation: loader-15 1s ease-in-out infinite;
    animation: loader-15 1s ease-in-out infinite;
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
    width: .25em;
    height: .5em;
    margin-left: 49%;
    font-size: 100px;
    /*line-height: 100px;*/
    margin-top: 5%;
    color: #646262;
  }

  .loader-15:after,
  .loader-15:before {
    content: '';
    position: absolute;
    width: inherit;
    height: inherit;
    background: inherit;
    -webkit-animation: inherit;
    animation: inherit;
  }

  .loader-15:before {
    right: .5em;
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
  }

  .loader-15:after {
    left: .5em;
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
  }

  @-webkit-keyframes loader-15 {
    0%,
    100% {
      box-shadow: 0 0 0 currentcolor, 0 0 0 currentcolor;
    }
    50% {
      box-shadow: 0 -.25em 0 currentcolor, 0 .25em 0 currentcolor;
    }
  }

  @keyframes loader-15 {
    0%,
    100% {
      box-shadow: 0 0 0 currentcolor, 0 0 0 currentcolor;
    }
    50% {
      box-shadow: 0 -.25em 0 currentcolor, 0 .25em 0 currentcolor;
    }
  }

  @media only screen and (min-width: 3000px) and (max-width: 4000px) {
    .box {
      font-size: 23px;
    }

    .box span {
      display: block;
      margin-top: 45%;
    }

    .loader-15 {
      margin-left: 49%;
      font-size: 60px;
      margin-top: 12%;
    }
  }

  @media only screen and (min-width: 4000px) and (max-width: 8000px) {
    .box {
      font-size: 8em;
    }

    .box span {
      display: block;
      margin-top: 15%;
    }

    .loader-15 {
      margin-left: 49%;
      font-size: 2em;
      margin-top: 5%;
    }
  }

  @media only screen and (min-width: 200px) and (max-width: 500px) {
    .box {
      font-size: 23px;
    }

    .box span {
      display: block;
      margin-top: 45%;
    }

    .loader-15 {
      margin-left: 49%;
      font-size: 60px;
      margin-top: 12%;
    }
  }

</style>



