<template>
  <div id="home">
    <div id="showForm">
      <transition name="toTop">
        <div id="menu-set" ref="mymenus" v-show="menu_flag" style="z-index: 10000">
          <Menus :list="menu_list" @Closemenu="Closemenu"></Menus>
          <button class="loginbtn" @click="showDowa" v-if="userInfo.token===null">
            <span class="iconfont">&#xe600;&nbsp;&nbsp;&nbsp;</span>登陆
          </button>
          <button class="loginbtn" @click="logout" v-else>退出</button>
        </div>
      </transition>
      <div class="overlay" v-show="menus_show" style="z-index: 9999"
           @click="Closemenu">
      </div>
      <div id="menu" v-show="menu">
        <div @click="showMenu"><span class="iconfont">&#xe65d;menu</span></div>
      </div>
      <div id="posts" ref="myposts">
        <Posts @showDowa="showDowa"></Posts>
      </div>
    </div>
    <transition name="fade">
      <div id="shadow" class="overlay" v-show="this.loginShwo.is_login">
        <div class="lightbox">
          <a id="gitbtn"
             href="https://github.com/login/oauth/authorize?client_id=25339c795b26b49a5e04">
            <span class="iconfont">&#xe600;</span>
          </a>
          <div class="close" @click="showDowa">
            <span id="close" class="iconfont">&#xe647;</span>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import Menus from './menu/Menus';
  import Posts from "./post/Posts";
  import Cookie from "../static/js/cookie"
  import About from './about/About';
  import {mapGetters} from 'vuex';
  import {categoryList} from "../api/api";

  export default {
    name: "Home",
    components: {
      Posts,
      Menus,
      About,
    },
    computed: {
      ...mapGetters({
        loginShwo: 'loginShwo',
        userInfo: 'userInfo'
      }),
    },
    data() {
      return {
        menu_list: [],
        menu_flag: false,
        menus_show: false,
        menu: true,
        screenWidth: document.body.clientWidth
      }
    },
    methods: {
      showMenu() {
        this.menu_flag = !this.menu_flag;
        this.menus_show = !this.menus_show;
        this.menu = !this.menu;
      },
      Closemenu() {
        this.menu_flag = !this.menu_flag;
        this.menus_show = !this.menus_show;
        this.menu = !this.menu;
      },
      showDowa() {
        this.$store.dispatch('showLogin');
        if (this.loginShwo.is_login) {
          $("#showForm").addClass('blur');
        } else {
          $("#showForm").removeClass('blur');
        }
      },
      initMenuData(response) {
        console.log(response)
        this.menu_list = response.data;
      },
      logout() {
        Cookie.delCookie('name');
        Cookie.delCookie('token');
        Cookie.delCookie('is_login');
        Cookie.delCookie('id');
        this.$store.dispatch('setInfo');
        this.$route.go(0);
      }
    },
    mounted() {
      categoryList().then(this.initMenuData);
    }
  }
</script>

<style lang="css" scoped>
  .blur {
    filter: blur(5px);
  }
  #home {
    position: relative;
  }

  #menu-set {
    overflow: auto;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 20%;
    height: 100%;
    box-shadow: 1px 1px 13px #888888;
    background: #7e7e7e;
    background-repeat: no-repeat;
  }

  #menu-set::-webkit-scrollbar {
    width: 1px;
    height: 100%;
  }

  .loginbtn {
    line-height: 3em;
    display: block;
    width: 60%;
    margin-left: 20%;
    margin-top: 60%;
    color: #fff;
    border: 1px solid #b7b7b7;
    border-radius: 8px;
    background: transparent;
    font-weight: 700;
    font-size: 1em;
    text-align: center;
    margin-bottom: 120px;
  }

  .loginbtn:hover {
    animation-name: myfirst;
    animation-duration: 1s;
    animation-direction: alternate;
    background: black;
    color: #FFF;
    position: relative;
  }

  .overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
  }

  #menu {
    position: fixed;
    top: 10px;
    left: 1%;
    height: 40px;
    width: 100px;
    text-align: center;
    z-index: 10000;
    color: #000;
    border: 1px solid #000;
    border-radius: 5px;
    background: rgba(215, 225, 229, 0.1);
  }

  #menu:hover {
    transition: 0.5s;
    transform: scale(1.1, 1.1);
    border: solid 1px black;
  }

  #menu div {
    margin-top: 8.5%;
    font-weight: 700;
  }

  #menu span {
    font-size: 20px;
  }

  #posts {
    overflow-y: auto;
    width: 100%;
  }

  .lightbox {
    position: absolute;
    z-index: 3;
    background-color: #FFF;
    margin-top: 10em;
    margin-left: 35%;
    border-radius: 10px;
    width: 500px;
    height: 400px;
  }

  #gitbtn {
    text-align: center;
    display: block;
    margin-top: 100px;
    width: 220px;
    height: 180px;
    margin-left: 140px;
    border-radius: 10px;
    background: #fff;
    border: none;
    text-decoration: none;
    padding-top: 20px;
  }

  #gitbtn span {
    font-size: 150px;
    color: black;
  }

  #gitbtn:hover {
    border: solid 1px #b7b7b7;
    box-shadow: 1px 1px 15px #888888;
    animation-name: myfirst;
    animation-duration: 1s;
    animation-direction: alternate;
  }

  .close {
    position: absolute;
    right: -25px;
    top: -25px;
    width: 50px;
    height: 50px;
    background: #FFF;
    border-radius: 25px;
    box-shadow: 2px 2px 5px 0 black;
    cursor: pointer;
    text-align: center;
    opacity: 1;
  }

  .close span {
    font-size: 32px;
    display: block;
    margin-top: 8px;
    color: black;
  }

  #close:hover {
    animation-name: myfirst;
    animation-duration: 1s;
    animation-direction: alternate;
    color: red;
  }

  @keyframes myfirst {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes identifier {
    from {
      transform: translateX(-400px);
    }
    to {
      transform: translateX(0px);
    }
  }

  @keyframes against {
    from {
      transform: translateX(0px);
    }
    to {
      transform: translateX(-400px);
    }
  }

  @media only screen and (min-width: 2000px) {
    #menu {
      height: 70px;
      width: 70px;
    }

    #menu span {
      font-size: 3.5em;
    }

    .lightbox {
      position: absolute;
      z-index: 3;
      background-color: #FFF;
      margin-top: 10em;
      margin-left: 30%;
      border-radius: 10px;
      width: 40%;
      height: 60%;
    }

    #gitbtn {
      display: block;
      margin-top: 15%;
      width: 60%;
      height: 60%;
      margin-left: 23%;
      border-radius: 40px;
    }

    #gitbtn span {
      font-size: 18em;
      color: black;
    }
  }

  @media only screen and (min-width: 5000px) and (max-width: 20000px) {
    #menu {
      height: 140px;
      width: 140px;
      border-radius: 30px;
    }

    #menu span {
      font-size: 7em;
    }

    /* 需要吸引用户注意的元素 */
    .lightbox {
      position: absolute;
      z-index: 3;
      background-color: #FFF;
      margin-top: 10em;
      margin-left: 30%;
      border-radius: 10px;
      width: 40%;
      height: 60%;
    }

    #gitbtn {
      display: block;
      margin-top: 15%;
      width: 60%;
      height: 60%;
      margin-left: 23%;
      border-radius: 100px;
    }

    #gitbtn span {
      font-size: 55em;
      color: black;
    }
  }

  @media only screen and (max-width: 900px) {
    .loginbtn {
      line-height: 3em;
      display: block;
      width: 60%;
      margin-left: 20%;
      margin-top: 60px;
      color: #919191;
      border: solid 1px #b7b7b7;
      box-shadow: 1px 1px 8px #888888;
      border-radius: 8px;
      background: #fff;
      font-weight: bold;
      font-size: 0.5em;
      text-align: center;
    }
  }

  @media only screen and (min-width: 200px) and (max-width: 500px) {
    .lightbox {
      position: absolute;
      z-index: 3;
      background-color: #FFF;
      margin-top: 10em;
      margin-left: 15%;
      border-radius: 10px;
      width: 70%;
      height: 300px;
    }

    #gitbtn {
      text-align: center;
      display: block;
      margin-top: 25%;
      width: 60%;
      height: 40%;
      margin-left: 23%;
      border-radius: 10px;
      background: #fff;
    }

    #gitbtn span {
      font-size: 100px;
      color: black;
    }

    #menu {
      height: 25px;
      width: 60px;
    }

    #menu span {
      font-size: 0.7em;
    }

    #menu div {
      /*margin-top: 4%;*/
      font-size: 0.5em;
    }

    #dingbu span {
      display: block;
      font-size: 35px;
    }

    #menu-set {
      width: 45%;
    }
  }

  .toTop-enter-active {
    animation: identifier .5s;
  }

  .toTop-leave-active {
    animation: against .4s;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity 1s;
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
  {
    opacity: 0;
  }

  @keyframes ridentifier {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(0%);
    }
  }

  @keyframes ragainst {
    from {
      transform: translateX(0%);
    }
    to {
      transform: translateX(100%);
    }
  }

  .rtoTop-enter-active {
    animation: ridentifier .4s;
  }

  .rtoTop-leave-active {
    animation: ragainst .4s;
  }
</style>



