<template>
  <div id="menu_list">
    <div id="img-t">
      <div>
        <img id="img" src="../../assets/1.jpeg"/>
      </div>
    </div>
    <span>菜单</span>
    <span v-if="userInfo.name">{{ userInfo.name }}~欢迎你</span>
    <div id="menus">
      <a @click="show_home">首页</a>
      <a v-for="item in list" :key="item.id" @click="Closemenu(item.id, item.name)">
        {{ item.name }}
      </a>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex';

  export default {
    name: "Menus",
    props: {
      list: Array,
    },
    computed: {
      ...mapGetters({
        userInfo: 'userInfo'
      })
    },
    data() {
      return {
        search_str: ''
      }
    },
    methods: {
      Closemenu(id, name) {
        this.$emit('Closemenu');
        console.log(id, name)
        this.$router.push({name:'Postlist', query:{category: id, name: name, type:'category'}});
      },
      show_home() {
        this.$emit('Closemenu');
        this.$router.push('/');
      },
      show_about() {
        this.$emit('Closemenu');
        this.$router.push('/about');
      }
    }
  }
</script>

<style lang="css" scoped>
  #menu_list {
    width: 100%;
  }

  #img-t {
    width: 100%;
    height: 10%;
    overflow: hidden;
    margin-bottom: 10%;
    background: rgba(0,0,0,0);
  }

  #img-t div {
    margin: 12.5% 0% 12.5%;
    overflow: hidden;
    text-align: center;
  }

  #img {
    width: 50%;
    border-radius: 90px;
  }

  #img:hover {
    transition: 1s;
    transform: scale(1.2, 1.2);
  }

  #img:link {
    transition: 0.3s;
  }

  a {
    display: block;
    line-height: 3em;
    text-decoration: none;
    color: #ffffff;
    font-size: 1em;
    text-align: center;
    outline-style: none;
  }

  a:hover {
    animation-name: myfirst;
    animation-duration: 1s;
    animation-direction: alternate;
    background: black;
  }

  span {
    margin-left: 25%;
    margin-right: 25%;
    font-size: 1em;
    color: #ffffff;
    display: block;
    margin-bottom: 10%;
    text-align: center;
    overflow: hidden;
  }
  input {
    margin: 0% 20% 10%;
    line-height: 30px;
    border-radius: 4px;
    border: none;
    font-size: 1em;
    width: 60%;
  }

  @keyframes myfirst {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @media only screen and (min-width: 2000px) {
    a {
      font-size: 3em;
    }
  }

  @media only screen and (max-width: 900px) {
    a {
      font-size: 0.6em;
    }
  }
  @media only screen and (min-width: 300px) and (max-width: 500px) {
    span {
      font-size: 0.5em;
    }
    input {
      margin: 0% 10% 10%;
      line-height: 20px;
      font-size: 0.6em;
      width: 80%;
    }
  }
</style>



