import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Postlist from '@/components/post/Postlist'
import Detail from '@/components/post/Detail'
import AuthGit from "@/components/auth/AuthGit"
import Vue404 from '@/components/error/Vue404'
import About from '@/components/about/About'

Vue.use(Router);

var router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '欢迎来到jummy的博客',
      },
      children: [
        {
          path: '',
          name: 'Postlist',
          component: Postlist,
          meta: {
            title: '博客列表'
          }
        },
        {
          path: 'detail/:id',
          name: 'Detail',
          component: Detail,
          props: true,
          meta: {
            title: '博客详情'
          }
        }
      ]
    },
    {
      path: '/AuthGit',
      name: 'AuthGit',
      component: AuthGit,
      meta: {
        title: '登陆'
      }
    },
    {
      path: '/404',
      name: '404',
      component: Vue404,
      meta: {
        title: '404了大哥'
      }
    },
    {
      path: '/about',
      name: 'About',
      component: About,
      meta: {
        title: '关于我'
      }
    },
  ]
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  if (to.matched.length == 0) {
    next({name: '404'});
  } else {
    next();
  }
});

export default router;



